import PropTypes from 'prop-types';
import React, { useContext } from 'react';
import ReactFlow, { Controls } from 'react-flow-renderer';

import HighlightedComponentsContext from '../../../contexts/HighlightedComponentsContext';
import ComponentNode from '../../atoms/ComponentNode';
import MiniMap from '../../atoms/MiniMap';

const ComponentTree = ({ elements }) => {
  const { setHighlightedId } = useContext(HighlightedComponentsContext);

  const highlightComponent = (_e, node) => {
    console.log('event: ', _e);
    console.log('node: ', node);
  };

  const resetHighlight = () => {
    setHighlightedId(null);
  };
  return (
    <>
      {elements && (
        <ReactFlow
          elements={elements}
          nodeTypes={{ reactComponent: ComponentNode }}
          onMouseOver={highlightComponent}
          onMouseLeave={resetHighlight}
        >
          <MiniMap />
          <Controls />
        </ReactFlow>
      )}
    </>
  );
};

ComponentTree.propTypes = {
  elements: PropTypes.any,
};

export default ComponentTree;
